import { QuestionTranslation } from './question-translation.model';
import { Answer } from './answer.model';

export const QuestionType = {
    singleChoice: 0, // Mt p n
    multiChoice: 1, // Nhiu p n
    vote: 2, // nh gi
    essay: 3, // T lun
    selfResponded: 4 // T tr li
};

export const QuestionStatus = {
    draft: 0, // Nhp
    pending: 1, // ang ch
    approved: 2, // ang duyt
    decline: 3 // Hy duyt
};

export const QuestionAnswerStatus = {
    hasAnswer: 0,
    noAnswer: 1,
    all: 2,
    correct: 3,
    inCorrect: 4,
}

export class Question {
    questionGroupId: number;
    questionType: number;
    concurrencyStamp: string;
    score: number;
    totalAnswer: number;
    isActive: boolean;
    answers: Answer[];
    modelTranslations: QuestionTranslation[];
    constructor() {
        this.isActive = true;

    }
}
